

.progressbarhome {
    height: 100%;
    line-height: 100px;
}

#progressbar {
    width: 99%;
    background-color: rgb(75, 74, 74);
    border-radius: 5px;
    border: 2px black solid;
    line-height: normal;
    display: inline-block;
    vertical-align: middle;
}

#progress {
    width: 0%;
    height: 20px;
    border-radius: 8px;
    background-color: rgb(25, 190, 255);
}